<template>
  <el-table
    :data="courseInfo"
    :stripe="true"
    style="width: 100%;" border>
    <el-table-column
      label="序号"
      header-align="center"
      align="center"
      type="index"
      width="40"
      >
    </el-table-column>
    <el-table-column
      prop="classNo"
      align="center"
      label="课程序号"
      header-align="center"
      min-width="35"
    >
    </el-table-column>
    <el-table-column
      prop="courseName"
      align="center"
      label="课程名"
      header-align="center"
      min-width="50"
      >
    </el-table-column>
    <el-table-column
      prop="teacher"
      align="center"
      label="教师"
      header-align="center"
      min-width="45"
    >
    </el-table-column>
    <el-table-column
      prop="college"
      align="center"
      label="开课学院"
      header-align="center"
      min-width="50"
    >
    </el-table-column>
    <el-table-column
      prop="category"
      align="center"
      label="课程类别"
      header-align="center"
      min-width="50"
    >
    </el-table-column>
    <el-table-column
      prop="className"
      align="center"
      label="教学班"
      header-align="center"
      min-width="75"
    >
    </el-table-column>
    <el-table-column
      prop="datePlace"
      align="center"
      label="时间地点"
      header-align="center"
      min-width="50"
     >
    </el-table-column>
    <el-table-column
      prop="classYear"
      align="center"
      label="学年学期"
      header-align="center"
      min-width="50"
    >
    </el-table-column>
    <el-table-column
      prop="action"
      align="center"
      label="操作"
      header-align="center"
      min-width="60"
       >
      <template slot-scope="scope">
        <div>
        <el-button class="el-button-local" @click="handleRegisterInfo(scope.$index, scope.row)" type="primary" size="mini">管理锁屏信息表</el-button>
        <el-button class="el-button-local"  type="primary" size="mini" style="margin-left: 0px">生成锁屏二维码</el-button>
        <el-button class="el-button-local" type="primary"  size="mini" style="margin-left: 0px">导出锁屏信息表</el-button>
        </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'component-table-class-manage',
  props: {
    courseInfo: Array
  },
  data () {
    return {
    }
  },
  methods: {
    handleRegisterInfo (index, row) {
      // console.log(index, row)
      this.$store.commit('setStoreCurrentClassNo', row.classNo)
      this.$store.commit('setStoreCurrentCourseName', row.courseName)
      this.$store.commit('setStoreCurrentClassName', row.className)
      // console.log(row.classNo)
      this.$router.push({path: '/RegisterTimeManagePage'})
    }
  }
}
</script>

<style type="text/css" scoped>

  .el-button-local{
    font-size: 11px;
    margin: 1px 0px;
    height: 25px;

  }

</style>



