<template>
  <el-container
    style="width:100%;height:100%;position: absolute;background-image: url('./static/img/loginbackground.jpg');background-repeat:no-repeat;background-size:100% 100%; "
  >
    <el-header
      style="width: 100%;height: 50px;background-color: rgba(253,255,255,0);"
    >
      <div style="float: left;padding-left: 5px"></div>
    </el-header>
    <el-main style="width: 100%;">
      <div style="width: 960px;height: auto;margin:30px auto;">
        <div style="display: inline-block;float: left;">

          <img
            src="static/img/headline.svg"
            type="image/svg+xml"
            style="height: 100px;"
          />
          <div style="display: inline-block">
          <div
            style="display: block;font-family: 細明體-ExtB;font-size: 70px;color: white;font-weight: bold;"
          >
            课堂狗
          </div>
          <div>
            <div style="color: white;font-size: 19px;">课&nbsp;堂&nbsp;宝&nbsp;贵 ，管&nbsp;住&nbsp;手&nbsp;机</div>

          </div>

          </div>
        </div>
        <div style="float:right;margin-right: 60px;">
          <el-card class="box-card" style="width: 400px;height:300px;">
            <div slot="header" class="clearfix">
              <div style="justify-content: center;font-size: 25px;">
                课堂狗在线考勤平台
              </div>
            </div>
            <div>
              <el-form ref="form" label-width="auto">
                <el-form-item>
                  <div>
                    <div style="display: inline-block;">
                      <i class="el-icon-user"></i>
                    </div>
                    <div style="display: inline-block;">
                      <el-input
                        v-model="user.userId"
                        placeholder="账号:"
                        style="width: 250px;height: 25px;margin-bottom: 0px;"
                      ></el-input>
                    </div>
                  </div>
                </el-form-item>
                <el-form-item>
                  <div style="display: inline-block;">
                    <div style="display: inline-block;">
                      <i class="el-icon-goods"></i>
                    </div>
                    <div style="display: inline-block;">
                      <el-input
                        v-model="user.userPassword"
                        placeholder="密码:"
                        style="width: 250px;height: 15px;"
                        show-password
                      ></el-input>
                    </div>
                  </div>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" style="width: 270px;height: 35px;"
                             @click="doLogin">登录</el-button
                  >
                </el-form-item>
              </el-form>
            </div>
            <div style="font-size: 12px;float: right;margin-top: 35px;">
              <a href="" style="color: #2b2c33;text-decoration:none;"
                >忘了密码?</a
              >&emsp;<i style="color:darkgrey">|</i>&emsp;<a
                href=""
                style="text-decoration:none;color: #2b2c33"
                >意见反馈</a
              >
            </div>
          </el-card>
        </div>
      </div>
    </el-main>
    <el-footer
      style="width:100%;background-color: rgba(255,251,253,0);position: fixed;bottom: 0;left: 0;right: 0;height: 35px;clear:both;"
    >
      <div style="margin-bottom: 1px;font-size: 12px;color:white;">
        Copyright © 2020 - 2025 .All Rights Reserved.<br />
        课堂宝开发组 版权所有
      </div>
    </el-footer>
  </el-container>
</template>

<script>
import axModule from 'axios'
export default {
  name: 'login',
  created () {
    if (this.$cookies.isKey('user_info')) {
      this.user = this.$cookies.get('user_info').user
    }
  },
  data () {
    return {
      user: {
        userId: '',
        userPassword: ''}
    }
  },
  methods: {
    doLogin () {
      if (this.user.userId === '') {
        alert('账户名不能为空')
        return false
      }
      if (this.user.userPassword === '') {
        alert('密码名不能为空')
        return false
      }

      axModule.get(this.GLOBAL.host + '/signIn', {
        params: {
          user_id: this.user.userId,
          user_password: this.user.userPassword
        }
      })
        .then(res => {
          console.log(res)
          if (res.status === 200) {
            if (res.data.code != null && res.data.code === '401') {
              this.$notify({
                title: '提示信息',
                message: '账号或密码错误',
                type: 'error'
              })
            } else {
              this.$cookies.set('user_info', {
                user_name: res.data,
                user: this.user}, 30 * 60)
              // this.$store.commit('setTeacherName', res.data)
              // this.$store.commit('setTeacherAccount', this.user.userId, this.userPassword)
              //   localStorage.userName = this.userInfo.userName
              //   localStorage.token_expire = res.data.expire
              //   localStorage.token = res.data.token
              this.$notify({
                title: '提示信息',
                message: '登录成功',
                type: 'success'
              })
              this.$router.push({path: '/homePage'})
            }
          } else {
            this.$notify({
              title: '提示信息',
              message: '服务器错误',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
        })
    }

  }
}
</script>

<style scoped></style>



