<template>
  <div>
    <head-factory :active='2' :user="user_info"></head-factory>
    <class-manage-factory :courseInfo="course_data"></class-manage-factory>
  </div>
</template>

<script>
import headFactory from '../../components/head/headFactory/headFactory'
import classManageFactory from '../../components/body/classManage/classManageFactory/classManageFactory'
import axModule from 'axios'
export default {
  name: 'classManagePage',
  components: {
    headFactory,
    classManageFactory
  },
  created () {
    if (this.$cookies.isKey('user_info')) {
      this.user_info = this.$cookies.get('user_info')
    } else {
      this.$router.push({path: '/login'})
    }

    axModule.get(this.GLOBAL.host + '/getCurrentTermClassInfo', {
      params: {
        user_id: this.user_info.user.userId,
        user_password: this.user_info.user.userPassword,
        Choose: this.$store.state.termInfo.clickChildrenTerm
      }
    }).then(res => {
      console.log(res)
      if (res.status === 200) {
        this.course_data = res.data
      }
    })
  },
  data () {
    return {
      user_info: {},
      course_data: []
    }
  }
}
</script>

<style scoped>

</style>



