<template>
  <div>
    <head-factory :active='1' :user="user_info"></head-factory>
    <home-factory></home-factory>
  </div>
</template>

<script>
import headFactory from '../../components/head/headFactory/headFactory'
import homeFactory from '../../components/body/home/homeFactory/homeFactory'
export default {
  name: 'messagePage',
  components: {
    headFactory,
    homeFactory
  },
  created () {
    // this.teacher_info = this.$store.getters.getTeacherAllInfo
    if (this.$cookies.isKey('user_info')) {
      this.user_info = this.$cookies.get('user_info')
    } else {
      this.$router.push({path: '/login'})
    }
  },
  data () {
    return {
      user_info: {}
    }
  },
  methods: {

  }
}
</script>

<style scoped>

</style>



