<template>
  <el-table
    :data="tableData"
    :stripe="true"
    style="width: 100%;" border>
    <el-table-column
      label="序号"
      header-align="center"
      type="index"
      align="center"
      width="180">
    </el-table-column>
    <el-table-column
      prop="title"
      align="center"
      label="通知标题"
      header-align="center"
      >
    </el-table-column>
    <el-table-column
      prop="time"
      align="center"
      label="发布时间"
      header-align="center"
      width="180">
    </el-table-column>
    <el-table-column
      prop="action"
      align="center"
      label="操作"
      header-align="center"
      width="180">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="primary" size="mini">查看</el-button>
        <el-button type="danger" size="mini">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'component-table',
  data () {
    return {
      tableData: [{
        title: '关于开展本科课堂教学考勤平台试用工作的通知',
        time: '2018-05-15 13:23:56'
      }]
    }
  }
}
</script>

<style scoped>
  el-table-column{
    justify-content: center;
    text-align: center;
  }

</style>



